.list {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
}

.item {
    width: 270px;
    padding: 10px;
    gap: 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    border: 1px solid #333;
    border-radius: 5px;
    transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
